import clsx from 'clsx';
import { Link } from 'react-router-dom';
import { useState } from "react";

import Modal from "./Modal";
import useModal from "../hooks/useModal";

function Requests() {

	const { isOpen, toggleModal } = useModal();

	const [isTopic, setIsTopic] = useState('');

	const arrRequests = [
		{
			id: 1,
			topic: '  1',
			number: 3168376,
			date: '10.04.23',
			time: '19:57',
			email: 'test@gmail.com',
			request: '       :     2  30.  ,    .      ,  .     :       .',
			label: 'progress',
			show: true,
			messagesInfo: {
				date: '10.04.23',
				time: '21:15',
				messages: [
					{
						user: true,
						message: '        :     2  30.      ,  .',
						time: '21:57'
					},
					{
						user: false,
						message: '       :     2  30.  ,    .      ,  .     :       ',
						time: '19:57'
					},
				]
			}
		},
		{
			id: 2,
			topic: '  2',
			number: 3168376,
			date: '10.04.23',
			time: '19:57',
			email: 'test@gmail.com',
			request: '       :     2  30.  ,    .      ,  .     :       .',
			label: 'done',
			show: true,
			messagesInfo: {
				date: '10.04.23',
				time: '21:15',
				messages: [
					{
						user: true,
						message: '       :     2  30.      ,  .',
						time: '06:22'
					},
					{
						user: false,
						message: '       :     2  30.  ,    .      ,  .     :       ',
						time: '05:22'
					},
				]
			}
		},
		{
			id: 3,
			topic: '  3',
			number: 3168376,
			date: '10.04.23',
			time: '19:57',
			email: 'test@gmail.com',
			request: '       :     2  30.  ,    .      ,  .     :       .',
			label: 'done',
			show: true,
			messagesInfo: {
				date: '10.04.23',
				time: '21:15',
				messages: [
					{
						user: false,
						message: '       :     2  30.      ,  .',
						time: '21:57'
					},
					{
						user: true,
						message: '       :     2  30.  ,    .      ,  .     :       ',
						time: '19:57'
					},
					{
						user: false,
						message: '       :     2  30.      ,  .',
						time: '18:57'
					},
					{
						user: true,
						message: '       :     2  30.  ,    .      ,  .     :       ',
						time: '17:57'
					},
				]
			}
		},
	];

	const arrTopic = [
		{
			topic: '  1',
		},
		{
			topic: '  2',
		},
		{
			topic: '  3',
		},
		{
			topic: '  4',
		},
		{
			topic: '  5',
		},
	];
	
	return (
		<div className="requests">
			<div className="requests__wrapper">
				<div className="requests__header">
					<h1 className="requests__header-title"></h1>
					<button className="requests__header-btn" onClick={toggleModal}>+  </button>
				</div>
				<div className={clsx(
						'requests__content',
						{'requests__content--empty': Object.values(arrRequests).filter(el=>el.show).length === 0}
					)}>
					{ (Object.values(arrRequests).filter(el=>el.show).length === 0) ? (
						<div className="requests__empty">   </div>
					) : (
						<ul className="requests__list">
							{arrRequests.map((request, index) => (
								(request.show === true) ? (
								<li key={index} className="requests__item">
									<button className="requests__item-btn-close">Close</button>
									<Link
										to = "/requests/request"
										state = {{ numRequest: request.id }}
        								className="requests__item-link">
										<div className="requests__item-header">
											<div className="requests__item-header-topic">{request.topic}</div>
											<div className="requests__item-header-info">{request.date} {request.time}</div>
											{ (request.label === 'progress') ? (
												<div className="requests__item-header-label requests__item-header-label--progress"></div>
												) : (request.label === 'done') ? (
													<div className="requests__item-header-label requests__item-header-label--done"></div>
												) : null
											}	
										</div>
										<div className="requests__item-content">  : {request.request}</div>
									</Link>
								</li> ) : null
							))}
						</ul>
					) }
				</div>
			</div>
			<Modal isOpen={isOpen} toggleModal={toggleModal}>
				<div className="modal-request">
					<div className="modal-request__title"> </div>
					<button className="modal-request__item-btn-close" onClick={toggleModal}>Close</button>
					<div className="modal-request__select">
						<input className="modal-request__select-input" type="text" value={isTopic} readOnly placeholder="  "/>
						<ul className="modal-request__select-list">
							{arrTopic.map((item, index) => (
								<li key={index} className="modal-request__select-item" onClick={() => setIsTopic(() => item.topic)}>
									<button className="modal-request__select-item-btn">{item.topic}</button>
								</li>
							))}
						</ul>
					</div>
					<div className="modal-request__btn-wrapper">
						<button className="modal-request__btn" onClick={toggleModal}>O</button>
						<button className="modal-request__btn" disabled={isTopic === '' ? true : false } onClick={toggleModal}> </button>
					</div>
				</div>
			</Modal>
		</div>
	);
}

export default Requests;