import clsx from 'clsx';
import { Link } from 'react-router-dom';
import { useState } from "react";

import Modal from "./Modal";
import useModal from "../hooks/useModal";

type RequestData = {
	numRequest: number
};

function Request(props: RequestData) {

	const { isOpen, toggleModal } = useModal();

	const [isTopic, setIsTopic] = useState('');

	const arrRequests = [
		{
			id: 1,
			topic: '  1',
			number: 3168376,
			date: '10.04.23',
			time: '19:57',
			email: 'test@gmail.com',
			request: '       :     2  30.  ,    .      ,  .     :       .',
			label: 'progress',
			show: true,
			messagesInfo: {
				date: '10.04.23',
				time: '21:15',
				messages: [
					{
						user: true,
						message: '        :     2  30.      ,  .',
						time: '21:57'
					},
					{
						user: false,
						message: '       :     2  30.  ,    .      ,  .     :       ',
						time: '19:57'
					},
				]
			}
		},
		{
			id: 2,
			topic: '  2',
			number: 3168376,
			date: '10.04.23',
			time: '19:57',
			email: 'test@gmail.com',
			request: '       :     2  30.  ,    .      ,  .     :       .',
			label: 'done',
			show: true,
			messagesInfo: {
				date: '10.04.23',
				time: '21:15',
				messages: [
					{
						user: true,
						message: '       :     2  30.      ,  .',
						time: '06:22'
					},
					{
						user: false,
						message: '       :     2  30.  ,    .      ,  .     :       ',
						time: '05:22'
					},
				]
			}
		},
		{
			id: 3,
			topic: '  3',
			number: 3168376,
			date: '10.04.23',
			time: '19:57',
			email: 'test@gmail.com',
			request: '       :     2  30.  ,    .      ,  .     :       .',
			label: 'done',
			show: true,
			messagesInfo: {
				date: '10.04.23',
				time: '21:15',
				messages: [
					{
						user: false,
						message: '       :     2  30.      ,  .',
						time: '21:57'
					},
					{
						user: true,
						message: '       :     2  30.  ,    .      ,  .     :       ',
						time: '19:57'
					},
					{
						user: false,
						message: '       :     2  30.      ,  .',
						time: '18:57'
					},
					{
						user: true,
						message: '       :     2  30.  ,    .      ,  .     :       ',
						time: '17:57'
					},
				]
			}
		},
	];

	const arrRequestsFilter = arrRequests.filter((info) => info.id === props.numRequest);

	const arrTopic = [
		{
			topic: '  1',
		},
		{
			topic: '  2',
		},
		{
			topic: '  3',
		},
		{
			topic: '  4',
		},
		{
			topic: '  5',
		},
	];
	
	return (
		<div className="request">
			<div className="request__wrapper">
				<div className="request__header">
					<h1 className="request__header-title"></h1>
					<button className="request__header-btn" onClick={toggleModal}>+  </button>
				</div>
				<div className="request__top">
					<Link className="request__top-btn" to="/requests/">Requests</Link>
					<div className="request__top-title"> </div>
				</div>
				{arrRequestsFilter.map((item, index) => (
					<div className="request__content">
						<div key={index} className="request__content-top">
							<p className="request__content-text"> : {item.number}</p>
							<p className="request__content-text"> : {item.date} {item.time}</p>
							<div className="request__content-label-wrapper">
								<p className="request__content-label-text">:</p>
								{ (item.label === 'progress') ? (
									<div className="request__label request__label--progress"> </div>
									) : (item.label === 'done') ? (
										<div className="request__label request__label--done"></div>
									) : null
								}
							</div>
							<p className="request__content-text"> : {item.email}</p>
							<p className="request__content-text">  : {item.request}</p>
							<p className="request__content-text"> :</p>
							<div className="request__content-photo-wrapper">
								<div className="request__content-photo request__content-photo--one"></div>
								<div className="request__content-photo request__content-photo--two"></div>
								<div className="request__content-photo request__content-photo--three"></div>
							</div>
							<p className="request__content-text"> :</p>
						</div>
						<div className="request__messages-wrapper">
							<div className="request__messages-date">{item.messagesInfo.date} {item.messagesInfo.time}</div>
							<div className="request__messages">
								{item.messagesInfo.messages.map((message, index) => (
									<div key={index} className={clsx(
										'request__message',
										{'request__message--answer': !message.user}
									)}>
										<div className="request__message-content">
											<div className="request__message-text">{message.message}</div>
											<div className="request__message-time">{message.time}</div>
										</div>
									</div>
								))}
							</div>
						</div>
					</div>	
				))}
			</div>
			<Modal isOpen={isOpen} toggleModal={toggleModal}>
				<div className="modal-request">
					<div className="modal-request__title"> </div>
					<button className="modal-request__item-btn-close" onClick={toggleModal}>Close</button>
					<div className="modal-request__select">
						<input className="modal-request__select-input" type="text" value={isTopic} readOnly placeholder="  "/>
						<ul className="modal-request__select-list">
							{arrTopic.map((item, index) => (
								<li key={index} className="modal-request__select-item" onClick={() => setIsTopic(() => item.topic)}>
									<button className="modal-request__select-item-btn">{item.topic}</button>
								</li>
							))}
						</ul>
					</div>
					<div className="modal-request__btn-wrapper">
						<button className="modal-request__btn" onClick={toggleModal}>O</button>
						<button className="modal-request__btn" disabled={isTopic === '' ? true : false } onClick={toggleModal}> </button>
					</div>
				</div>
			</Modal>
		</div>
	);
}

export default Request;